import React from "react";
import SignInFormContainer from 'data/redux/containers/signInFormContainer/SignInFormContainer';
import AuthenticationCard from '../authenticationCard';

export interface IInfoCardSquare {
  toggleLogin: () => void;
}

const Login = (props: IInfoCardSquare) => (
  <AuthenticationCard
    title=" "
    switchCardComponent={<span style={{color: "#585858"}}>
          ?
        <span onClick={() => props.toggleLogin()} style={{cursor: "pointer", color: "#F28F17"}}>   </span>
        </span>}
    authenticateComponent={<SignInFormContainer/>}
  />
);

Login.defaultProps = {
  toggleLogin: () => {
    
  },
};

export default Login;
