import React from "react";
import SignUpFormContainer from 'data/redux/containers/signUpFormContainer/SignUpFormContainer';
import AuthenticationCard from '../authenticationCard/AuthenticationCard';

export interface IInfoCardSquare {
  toggleLogin: () => void;
}

const Register = (props: IInfoCardSquare) => (
  <AuthenticationCard
    title="   "
    switchCardComponent={<span style={{color: "#585858"}}>
          ?
        <span onClick={() => props.toggleLogin()} style={{cursor: "pointer", color: "#F28F17"}}>  </span>
         
        </span>}
    authenticateComponent={<SignUpFormContainer nextText={''}/>}
  />
);

Register.defaultProps = {
  toggleLogin: () => {
    
  },
};

export default Register;
