import React from 'react';
import * as Yup from "yup";
import {Grid} from '@material-ui/core';
import HotelDetails from './components/hotelDetails/HotelDetails';
import PriceDetails from './components/priceDetails/PriceDetails';
import {getTodayMidnight} from 'data/utils/shortcuts';
import OfferDetails from './components/offerDetails';
import Conditions from '../../../commons/editor/components/abstractProductDetailsForm/components/conditions';
import AbstractProductDetailsForm from '../../../commons/editor/components/abstractProductDetailsForm';
import {IHotelDetails} from 'data/interfaces/products';

export interface IHotelDetailsWithTerms extends IHotelDetails {
  acceptedTerms: boolean;
}

interface IHotelDetailsFormProps {
  hotelDetails?: IHotelDetailsWithTerms;
  onSubmit: (formValues) => Promise<any>;
}

export const hotelDetailsYupSchemaObject = Yup.object({
  hotel: Yup.object({
    id: Yup.number(),
    place_id: Yup.string().min(1).max(1000).required("  ")
  }).required("  "),
  imagePick: Yup.array().min(1, "    ").max(30).required(),
  checkInDate: Yup.number().test("checkInDate", "      ",
    checkInDate => getTodayMidnight().getTime() <= checkInDate)
    .required("    ").nullable(),
  checkOutDate: Yup.number().required("    ").nullable()
    .when('checkInDate', (checkInDate, schema) => schema.test({
      test: checkOutDate => checkOutDate >= checkInDate,
      message: "        "
    })),
  // roomType: Yup.number().min(1, "   ").max(10).required("   "),
  roomsAmount: Yup.number().min(1, "   ").max(20, "     - 20").required("   "),
  roomPeopleAmount: Yup.number().min(1, "   ").max(20, "     - 20").required("   "),
  roomMeal: Yup.number().min(1, "   ").max(10).required("   "),
  hotelDescription: Yup.string().max(10000),
  changePrice: Yup.number()
    .min(0, "     ")
    .max(999999, "     -999999")
    .required("       ,    ,   0"),
//  cancelPrice: Yup.number()
//    .min(0, "     ")
//    .max(999999, "     -999999")
//    .required("   ,     0"),
  totalHotelPaidPrice: Yup.number()
    .min(1, "         -1")
    .max(999999, "         -999999")
    .required("       "),
  //hotelToBePaidPrice: Yup.number()
  //  .min(0, "       -0")
  //  .max(999999, "       -999999")
  //  .required("     "),
  // vacationType: Yup.number().required("   "),
  currency: Yup.number().min(1, "  ").max(10).required("  "),
  acceptedTerms: Yup.boolean()
    .required("Required")
    .oneOf([true], "     "),
}).noUnknown(true);

export const hotelDetailsInitialValues = {
  imagePick: [],
  hotel: undefined,
  currency: 2,
  checkInDate: null,
  checkOutDate: null,
  hotelDescription: "",
  roomMeal: undefined,
  roomsAmount: 1,
  roomPeopleAmount: 1,
  changePrice: undefined,
//  cancelPrice: undefined,
  totalHotelPaidPrice: undefined,
  acceptedTerms: false,
//  vacationType: "",
};

export const HotelDetailsInputs = ({
                                     submitForm,
                                     submitCount,
                                     isSubmitting,
                                     setFieldValue,
                                     values,
                                     touched,
                                     errors
                                   }) => <>
  <HotelDetails values={values} submitCount={submitCount} setFieldValue={setFieldValue} errors={errors}/>
  <Grid item xs={12} style={{marginTop: 52}}/>
  <OfferDetails setFieldValue={setFieldValue} values={values as any}/>
</>;

const HotelDetailsForm = (props: IHotelDetailsFormProps) => (<AbstractProductDetailsForm
  {...props}
  initialValues={{
    ...hotelDetailsInitialValues,
    ...props.hotelDetails
  }}
  yupSchemaObject={hotelDetailsYupSchemaObject}
  formComponents={({submitForm, submitCount, isSubmitting, setFieldValue, values, touched, errors}) => (
    <>
      <HotelDetailsInputs {...{submitForm, submitCount, isSubmitting, setFieldValue, values, touched, errors}}/>
      <Grid item xs={12} style={{marginTop: 0}}/>
      <PriceDetails values={values}/>
      <Grid item xs={12} style={{marginTop: 30}}/>
      <Conditions values={values}/>
    </>
  )}
  onSubmit={(formValues) => props
    .onSubmit({
      hotelDetails: formValues
    })}
/>);

HotelDetailsForm.defaultProps = {
  hotelDetails: {},
};

export default React.memo(HotelDetailsForm);
